div {
  box-sizing: border-box;
}

.App {
  text-align: center;
}

.nav-container {
  display: flex;
  background-color: black;
}

.blog-container {
  padding: 20px;
  /* border-bottom: 0.5px solid black; */
}

.blog {
  display: flex;
  align-items: center;
}

.blog-contents {
  display: flex;
  border-bottom: 0.5px solid black;
  padding-bottom: 20px;
}

.modal_container{
  background-color: #eee;
  padding-left: 20px;

}

.modal {
  display: flex;
  align-items: start;
  justify-content: center;
  padding: 4px;
  gap: 4px;
  flex-direction: column; /* 정렬 방향 세로로*/
}

.black-nav {
  background-color: black;
  width: 100%;
  color: white;
  padding: 20px;
  display: flex;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
